        wordToWord: 'అర్జునః ఉవాచ — అర్జునుడు పలికెను; మత్-అనుగ్రహాయ — నా మీద దయ ఉంచి; పరమం — సర్వోన్నతమైన; గుహ్యం — రహస్యమైన; ఆధ్యాత్మ-సంజ్ఞితమ్ — ఆధ్యాత్మిక జ్ఞానము గురించి; యత్ — ఏదైతే; త్వయా — నీ చే; ఉక్తం — చెప్పబడెనో; వచః — వాక్యములు; తేన — దానిచే; మోహః — మోహము; అయం — ఇది; విగతః — పటాపంచలయినది; మమ — నా యొక్క.',
