        translation: 'BG 11.4: ఓ యోగేశ్వరా, నాకు దాన్ని దర్శించగలిగే శక్తి ఉంది అని నీవు అనుకుంటే, దయచేసి ఆ యొక్క నిత్య శాశ్వతమైన విశ్వ రూపమును నాకు చూపించుము.',
    
