        translation: 'BG 17.5-6: కొంతమంది జనులు, అత్యంత కఠినమైన తపస్సులను, శాస్త్రవిరుద్ధమైనా, తమ దంభం(కపటత్వం) మరియు అహంకారముచే ప్రేరితులై చేస్తారు. కామము మరియు మమకారముచే ప్రేరితులై, వారు తమ శరీర అవయములనే కాక, వారి శరీరములోనే పరమాత్మగా ఉన్న నన్ను కూడా క్షోభ పెడతారు. ఇటువంటి బుద్ధిహీనులు ఆసురీ గుణసంకల్పంతో ఉన్నవారని తెలుసుకొనుము.',
    
