        wordToWord: 'దంభః — దంభము; దర్పః — దురహంకారము; అభిమానః — అహంభావం; చ — మరియు; క్రోధః — కోపము; పారుష్యం — మొరటుతనము; ఏవ — నిజముగా; చ — మరియు; అజ్ఞానం — అజ్ఞానము; చ — మరియు; అభిజాతస్య — కలిగి ఉండేవారు; పార్థ — అర్జునా, ఓ ప్రిథ పుత్రుడా; సంపదం — గుణములు; ఆసురీమ్ — ఆసురీ (రాక్షస).',
