        wordToWord: 'తస్మాత్ — కాబట్టి; శాస్త్రం — శాస్త్రములు; ప్రమాణం — ప్రమాణములు; తే — నీకు; కార్య — విధులు (చేయవలసినవి); అకార్యా — నిషేధింపబడిన కార్యములు; వ్యవస్థితౌ — నిర్ణయించుకోవటానికి; జ్ఞాత్వా — తెలుసుకొన్న పిదప; శాస్త్ర — శాస్త్రముల; విధాన — ఉపదేశము; ఉక్తం — తెలియచేయబడిన విధముగా; కర్మ — కర్మలు; కర్తుమ్ — చేయుట; ఇహ — ఈ జగత్తులో; అర్హసి — అర్హుడవు.',
        translation: 'BG 16.24: కాబట్టి, ఏది చేయాలి ఏది చేయకూడదు అన్న విషయంలో శాస్త్రములనే ప్రమాణముగా తీసుకొనుము. శాస్త్ర విధివిధానాలు, ఉపదేశాలను తెలుసుకొనుము మరియు ఆ విధంగానే ఈ జగత్తులో ప్రవర్తించుము.',
    
