        wordToWord: 'మాం — నా యందే; హి — నిజముగా; పార్థ — అర్జునా, ప్రిథ పుత్రుడా; వ్యపాశ్రిత్య — ఆశ్రయమును పొంది (శరణుజొచ్చి); యే — ఎవరైతే; అపి — అయినా సరే; స్యుః — అయినా కూడా; పాప యోనయః — నిమ్న స్థాయి జన్మ; స్త్రియః — స్త్రీలు; వైశ్యః — వైశ్యులు; తథా — మరియు; శూద్రః — కార్మికులు; తే అపి — వారు కూడా; యాంతి — వెళ్ళెదరు; పరాం — పరమ (సర్వోన్నత); గతిం — గమ్యమును.',
