        transliteration: 'అర్జున ఉవాచ । సన్న్యాసం కర్మణాం కృష్ణ పునర్యోగం చ శంససి । యఛ్చ్రేయ ఏతయోరేకం తన్మే బ్రూహి సునిశ్చితమ్ ।। 1 ।।',
        wordToWord: 'అర్జున ఉవాచ — అర్జునుడు పలికెను; సన్న్యాసం — సన్యాసము; కర్మణాం — కర్మల యొక్క; కృష్ణ — శ్రీ కృష్ణ; పునః — మరల; యోగం — కర్మ యోగ గురించి; చ — మరియు; శంససి — ప్రశంసించావు; యత్ — ఏదైతే; శ్రేయః — ఎక్కువ శేయస్కరమో; ఏతయో — ఈ రెంటిలో; ఏకం — ఒకటి; తత్ — అది; మే — నాకు; బ్రూహి — దయచేసి చెప్పుము; సు-నిశ్చితమ్ — నిర్ణయాత్మకముగా.',
