        transliteration: 'జ్ఞేయః స నిత్యసంన్యాసీ యో న ద్వేష్టి న కాంక్షతి । నిర్ద్వందో హి మహాబాహో సుఖం బంధాత్ప్రముచ్యతే ।। 3 ।।',
        wordToWord: 'జ్ఞేయః — పరిగణించవలెను; సః — అతను; నిత్య — ఎల్లప్పుడూ; సంన్యాసీ — సన్యాస దీక్ష ను ఆచరిస్తూ; యః — ఎవరైతే; న, ద్వేష్టి — దేనినీ ద్వేషింపక; న, కాంక్షతి — దేనినీ ఆశించక; నిర్ద్వంద్వః — అన్ని ద్వందములకు అతీతంగా; హి — నిజముగా; మహా-బాహో — గొప్ప బాహువులు కలవాడా; సుఖం — సునాయాసముగానే; బంధాత్ — బంధము నుండి; ప్రముచ్యతే — విముక్తి చేయబడును.',
