        transliteration: 'సాంఖ్యయోగౌ పృథగ్బాలాః ప్రవదంతి న పండితాః । ఏకమప్యాస్థితః సమ్యక్ ఉభయోర్విందతే ఫలమ్ ।। 4 ।।',
