        wordToWord: 'సన్యాసః — సన్యాసము; తు — కానీ; మహా-బాహో — గొప్ప బాహువులు కలవాడా; దుఃఖమ్ — దుఃఖము; ఆప్తుమ్ — పొందును; అయోగతః — కర్మ యోగము లేకుండా; యోగ-యుక్తః — కర్మ యోగములో ప్రవీణుడు; మునిః — ముని; బ్రహ్మ — బ్రహ్మన్; న చిరేణ — త్వరితముగా ; అధిగచ్ఛతి — చేరును.',
