        wordToWord: 'శ్రీ- భగవాన్-ఉవాచ — భగవానుడు ఇలా పలికెను; పరం — సర్వోన్నత; భూయః  — మరల; ప్రవక్ష్యామి — నేను వివరిస్తాను; జ్ఞానానాం — అన్ని జ్ఞానములోకెల్లా; జ్ఞానమ్-ఉత్తమమ్ — సర్వోన్నత జ్ఞానము; యత్  — ఏదైతే; జ్ఞాత్వా — తెలుసుకున్న పిదప; మునయః  — మునులు; సర్వే — అందరూ; పరాం — అత్యున్నత; సిద్ధిమ్ — పరిపూర్ణతను (సిద్ధిని); ఇతః — దీని ద్వారా; గతాః  — పొందినారు.',
        translation: 'BG 14.1: శ్రీ భగవానుడు పలికెను: నేను మళ్ళీ ఒకసారి ఈ యొక్క సర్వశ్రేష్టమైన విద్యను, అన్నింటికన్నా ఉత్తమమైన  జ్ఞానమును నీకు వివరిస్తాను; ఇది తెలుసుకున్న గొప్ప సాధువులందరూ అత్యున్నత పరిపూర్ణతను సాధించారు.',
    
