        transliteration: 'మమ యోనిర్మహద్బ్రహ్మ తస్మిన్గర్భం దధామ్యహమ్ । సంభవః సర్వభూతానాం తతో భవతి భారత ।। 3 ।। సర్వయోనిషు కౌంతేయ మూర్తయః సంభవంతి యాః । తాసాం బ్రహ్మ మహద్యోనిః అహం బీజప్రదః పితా ।। 4 ।।',
