        transliteration: 'మమ యోనిర్మహద్బ్రహ్మ తస్మిన్గర్భం దధామ్యహమ్ । సంభవః సర్వభూతానాం తతో భవతి భారత ।। 3 ।। సర్వయోనిషు కౌంతేయ మూర్తయః సంభవంతి యాః । తాసాం బ్రహ్మ మహద్యోనిః అహం బీజప్రదః పితా ।। 4 ।।',
        wordToWord: 'మమ — నా యొక్క; యోనిః — గర్భము; మహత్ బ్రహ్మ — సమస్త భౌతిక పదార్ధము, ప్రకృతి; తస్మిన్ — దానిలో; గర్భం — గర్భము; దధామి — ఉంచెదను (గర్భాదానము); అహం — నేను; సంభవః — పుట్టుట; సర్వభూతానాం — సమస్త ప్రాణులు; తతః — ఆ విధముగా; భవతి — జరుగును; భారత — అర్జునా, భరత వంశీయుడా; సర్వ — సమస్త; యోనిషు — జీవ రాశులు; కౌంతేయ — అర్జునా, కుంతీ దేవి పుత్రుడా; మూర్తయః — రూపములు; సంభవంతి — సంభవించును (జనించును); యాః తాసాం —  అవి అన్నింటిలో కూడా; బ్రహ్మ-మహత్ — ఈ గొప్ప భౌతిక ప్రకృతి; యోనిః  — గర్భము; అహం — నేను; బీజ-ప్రదః — బీజమును ఇచ్చే; పితా — తండ్రిని.',
